@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300ita‌​lic,400italic,500,500italic,700,700italic,900italic,900);


/*---------------------------------------
COMMON STYLE       
-----------------------------------------*/

html,
body,
html * {
	line-height: 1.5;
	font-family: 'Roboto', sans-serif;
}

body {
	overflow-x: hidden;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Roboto', sans-serif;
}

.fixed-bg {
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-position: center center;
}

.overlay {
	width: 100%;
	height: 100%;
}

.black-overlay {
	background-color: rgba(0, 0, 0, .8);
}

p {
	color: #585858;
	font-size: 14px;
	line-height: 26px;
}

/*---------------------------------------
SECTION COMMON STYLE       
-----------------------------------------*/

.section {
	position: relative;
	padding-top: 100px;
	padding-bottom: 100px;
}

.section-title {
	padding-bottom: 70px;
}

.section-title h4 {
	color: #585858;
	font-size: 9px;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: 3px;
	margin: 0px;
	font-family: 'Merriweather', serif;
}

.section-title h2 {
	margin: 0px;
	margin-top: 5px;
	font-size: 20px;
	font-weight: 700;
	text-transform: uppercase;
	color: #333;
	position: relative;
	letter-spacing: 2px;
}

.section-title p {
	font-size: 16px;
	margin-top: 40px;
	font-weight: 300;
	color: #454850;
}


/*----------------------
 NAVIGATION
-----------------------*/

.top-nav {
	border-bottom: 1px solid rgba(255, 255, 255, 0.2);
	height: 80px;
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

.top-nav .navbar-nav li .nav-link {
	color: #fff;
	font-size: 14px;
	padding: 29px 15px;
	letter-spacing: 1.5px;
	/* font-weight: 600; */
	text-transform: uppercase;
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

.top-nav li a.nav-link:hover,
.top-nav .nav-item.active a.nav-link {
	border-bottom: 3px solid #1cbac8;
	color: #1cbac8;
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

.top-nav .navbar-brand img {
	padding: 0px;
	width: 100%;
	height: auto;
}

.top-nav .navbar-brand {
	background: url(./img/logo.png) center / contain no-repeat;
	width: 100px;
}

.top-nav.light-header {
	height: 60px;
	background: #fff;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.23);
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

.top-nav.light-header .navbar-brand {
	background: url(./img/logo_light.png) center / contain no-repeat;
}

.top-nav.light-header .navbar-nav li .nav-link {
	color: #212121;
	padding: 19px 15px;
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

.top-nav.light-header li a.nav-link:hover,
.top-nav.light-header .nav-item.active a.nav-link {
	border-bottom: 3px solid #1cbac8;
	color: #1cbac8;
	transition: 0.5s ease-in;
	-webkit-transition: 0.5s ease-in;
	-moz-transition: 0.5s ease-in;
}

/* ========== Responsive Stylings ========== */

@media (max-width: 767px) {
	.top-nav .navbar-toggler i {
		color: #fff;
		margin-top: 6px;
	}

	.top-nav.light-header .navbar-toggler i {
		color: #1cbac8;
	}

	.top-nav .navbar-collapse.show {
		background-color: rgba(255, 255, 255, 0.9);
		text-align: center;
		padding: 12px 0;
	}

	.top-nav .navbar-collapse.show a {
		color: #000;
	}
}



/*----------------------
 HEADER
-----------------------*/

.header {
	background: url(./img/header.jpg);
	background-size: cover;

	background-attachment: fixed;
	background-repeat: no-repeat;
	color: #cfcfcf;
}

.header .overlay {
	background: -moz-linear-gradient(top,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 27%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* FF3.6+ */
	background: -webkit-gradient(linear,
			left top,
			left bottom,
			color-stop(0%, rgba(0, 0, 0, 0.8)),
			color-stop(17%, rgba(0, 0, 0, 0.73)),
			color-stop(35%, rgba(0, 0, 0, 0.66)),
			color-stop(62%, rgba(0, 0, 0, 0.55)),
			color-stop(100%, rgba(0, 0, 0, 0.4)));
	/* Chrome,Safari4+ */
	background: -webkit-linear-gradient(top,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 17%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(top,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 17%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* Opera 11.10+ */
	background: -ms-linear-gradient(top,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 17%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* IE10+ */
	background: linear-gradient(to bottom,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 17%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#cc000000',
			endColorstr='#66000000',
			GradientType=0);
	/* IE6-9 */
	height: 100vh;
	background-attachment: fixed;
}

.header-content {
	padding-top: 35vh;
	padding-left: 10vh;
}

.header-content h4 {
	font-size: 18px;
	font-weight: 300;
	line-height: 60px;
	margin: 0px;
	color: #fff;
	letter-spacing: 2px;
	text-transform: uppercase;
}

.header-content h2 {
	color: #fff;
	font-size: 60px;
	font-weight: 400;
	letter-spacing: 10px;
	margin: 0;
	text-transform: uppercase;
	margin-bottom: 50px;
}

.header-content h2 span {
	color: #1cbac8;
	text-shadow: 2px 2px 0 rgba(0, 0, 0, .75);
}

.header_btn,
.header_btn:focus,
.header_btn:active,
.header_btn:visited {
	color: #fff;
	font-size: 0.8rem;
	font-weight: 400;
	border: 1px solid #fff;
	border-radius: 0px;
	padding: 12px 40px;
	text-transform: uppercase;
	letter-spacing: 1px;
	margin-right: 10px;
	background-color: transparent;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
	width: 185px;
}

.header_btn:hover {
	color: #fff;
	background-color: #1cbac8;
	border-color: #1cbac8;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

@media (max-width: 768px) {

	.header-content h4 {
		font-size: 1.1rem;
		font-weight: 200;
	}

	.header-content h2 {
		font-size: 3.5rem;
		margin-bottom: 4rem;
		margin-top: 2rem;
	}

	.header-content h2 span {
		display: block;
		line-height: 3rem;
		height: 50px;
	}

	.header-content h4 span {
		display: block;
		line-height: 2rem;
	}

	.header_btn {
		font-size: 0.75rem;
		margin-bottom: 0.5rem;
		width: 10rem;
	}

}

@media (min-width: 320px) and (max-width: 480px) {
	.header-content h4 {
		font-size: 0.75rem;
		font-weight: 200;
	}

	.header-content h2 {
		font-size: 2rem;
		margin-bottom: 2rem;
		margin-top: 2rem;
	}

	.header-content h2 span {
		line-height: 2rem;
	}

	.header-content h4 span {
		line-height: 1.3rem;
	}

	.header_btn {
		font-size: 0.7rem;
	}

}

@media only screen and (min-device-width : 500px) and (max-device-width : 1024px) and (orientation : landscape) {
	.header-content h4 {
		font-size: 0.75rem;
		font-weight: 200;
	}

	.header-content h2 {
		font-size: 3rem;
		margin-bottom: 0.7rem;
	}

	.header-content h2 span {
		display: inline;
		line-height: 2rem;
	}

	.header-content h4 span {
		line-height: 1.3rem;
		display: inline;
	}

	.header_btn {
		font-size: 0.7rem;
		padding: 0.7rem 0.5rem;
		width: 10rem;
	}


}


/*----------------------
 SERVICE SECTION
-----------------------*/

.single-service {
	position: relative;
	text-align: center;
	margin-bottom: 50px;
	-webkit-transition: .3s;
	transition: .3s;
	padding: 30px 20px;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16)
}

.single-service:before {
	position: absolute;
	width: 0;
	height: 0;
	background-color: #1cbac8;
	left: 0;
	top: 0;
	content: "";
	-webkit-transition: .3s;
	transition: .3s
}

.single-service:after {
	position: absolute;
	width: 0;
	height: 0;
	background-color: #1cbac8;
	right: 0;
	bottom: 0;
	content: "";
	-webkit-transition: .3s;
	transition: .3s
}

.single-service:hover:after,
.single-service:hover:before {
	width: 50%;
	height: 2px;
	-webkit-transition: .3s;
	transition: .3s
}

.single-service:hover {
	box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, 0.10)
}

.single-service i {
	font-size: 30px;
	width: 80px;
	height: 80px;
	color: #1cbac8;
	border: 1px solid #ddd;
	line-height: 80px;
	margin-bottom: 30px;
	border-radius: 50%;
	-webkit-transition: .3s;
	transition: .3s
}

.single-service:hover i {
	background-color: #1cbac8;
	color: #fff;
	border-color: #1cbac8;
	border-radius: 0;
}

.single-service h4 {
	font-size: 20px;
	font-weight: 400;
	margin-bottom: 15px;
	text-transform: capitalize;
}

.single-service p {
	font-size: 15px;
	line-height: 1.8;
}

/*---------------------------------------
FACTS SECTION       
-----------------------------------------*/
.facts {
	background-image: url('./img/header.jpg');
	background-size: cover;
}

.facts .overlay {
	padding-top: 120px;
	padding-bottom: 120px;
}

.single-fact i {
	color: #1cbac8;
	font-size: 40px;
}

.single-fact:hover {
	transition: transform .5s ease;
	transform: scale(1.2);
}

.single-fact i:hover {
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
	-webkit-transition: .5s all ease;
	-moz-transition: .5s all ease;
	transition: .5s all ease;
}

.single-fact h2 {
	font-size: 30px;
	font-weight: 600;
	color: #fff;
	letter-spacing: 1px;
	margin: 0px;
	margin-top: 30px;
	margin-bottom: 10px;
}

.single-fact p {
	font-size: 12px;
	text-transform: uppercase;
	color: #cacaca;
	letter-spacing: 2px;
	margin: 0px;
}

@media (max-width: 991px) {
	.single-fact {
		padding-bottom: 40px;
	}
}

/*---------------------------------------
NEW PROJECTT       
-----------------------------------------*/
.project {
	background-color: #19a5b2;
	padding-top: 50px;
	padding-bottom: 50px;
}

.project-text {}

.project-text i {
	color: #fff;
	font-size: 40px;
}

.project-text i:hover {
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
	-webkit-transition: .5s all ease;
	-moz-transition: .5s all ease;
	transition: .5s all ease;
}

.project h2 {
	margin: 0px;
	font-size: 14px;
	text-transform: uppercase;
	color: #fff;
	font-weight: 600;
	letter-spacing: 2px;
	margin-top: 15px;
}

.btn-project,
.btn-project:active,
.btn-project:visited,
.btn-project:focus {
	background-color: transparent;
	border: 2px solid #fff;
	color: #fff;
	text-transform: uppercase;
	font-weight: 600;
	border-radius: 0px;
	padding: 7px 20px;
	box-shadow: none;
	outline: 0 !important;
	text-align: right;
	margin-top: 4px;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.btn-project:hover {
	background-color: #fff;
	color: #333;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.project .col-md-5 {
	text-align: right;
}

@media (max-width: 768px) {
	.project-text {
		text-align: center;
	}

	.project h2 {
		text-align: center;
	}

	.h_p_button {
		text-align: center;
		padding-top: 15px;
	}
}

/*---------------------------------------
TESTIMONIAL SECTION            
-----------------------------------------*/

.testimonial-bg {
	background-image: url('./img/header.jpg');
	background-size: cover;
}

.testimonial-overlay {
	background-color: rgba(0, 0, 0, .8);
	padding-top: 100px;
	padding-bottom: 100px;
}

.testimonial-title {
	padding-bottom: 40px;
}

.testimonial-title h2 {
	color: #fff;
	font-size: 16px;
	letter-spacing: 3px;
	margin: 0px;
	margin-top: 15px;
}

.testimonial-rating {
	color: #fff;
	font-size: 20px;
	letter-spacing: 3px;
	margin: 0px;
	margin-bottom: 15px;
}

.testimonial-rating:before {
	font-family: 'FontAwesome';
	content: "\2605 \2605 \2605 \2605 \2605";
	white-space: pre;
}

.testimonial-caption {
	width: 100%;
	margin: 0;
	height: 160px;
}

.testimonial-caption p {
	color: #cacaca;
	font-size: 14px;
	line-height: 30px;
}

.testimonial-caption h4 {
	font-size: 11px;
	color: #cacaca;
	font-weight: 400;
	margin-top: 25px;
	letter-spacing: 2px;
}

.testimonial-caption h4 {
	color: #3fb0ac;
	text-decoration: none;
	text-transform: uppercase;
}

.testimonial .carousel-indicators {
	bottom: -50px;
}

.testimonial .carousel-indicators li {
	margin: 3px;
	width: 10px;
	height: 10px;
	border-radius: 100%;
	border: 2px solid #fff;
	background-color: #fff;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.testimonial .carousel-indicators .active {
	margin: 3px;
	background-color: #1cbac8;
	border: 2px solid #1cbac8;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

/*---------------------------------------
CLIENTS SECTION            
-----------------------------------------*/
.clients .carousel-indicators {
	bottom: -50px;
}

.clients .carousel-indicators li {
	margin: 3px;
	width: 10px;
	height: 10px;
	border-radius: 100%;
	border: 2px solid rgb(202, 202, 202);
	background-color: rgb(202, 202, 202);
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.clients .carousel-indicators .active {
	margin: 3px;
	background-color: #1cbac8;
	border: 2px solid #1cbac8;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.clients img {
	filter: gray;
	-webkit-filter: grayscale(1);
	-webkit-transition: all .8s ease-in-out;
	width: 7.5rem;
}

.clients img:hover {
	filter: none;
	-webkit-filter: grayscale(0);
	-webkit-transform: scale(1.1);
}


/*---------------------------------------
           
-----------------------------------------*/

.portfolio {
	position: relative;
	overflow: hidden;
}
.portfolio img {
	width: 100%;
	-webkit-transform: scale(1);
	-moz-transform: scale(1);
	-ms-transform: scale(1);
	/* transform: scale(1); */
	-webkit-transition: 0.3s;
	-moz-transition: 0.3s;
	-o-transition: 0.3s;
	/* transition: 0.3s; */
	transform: rotate(0deg) scale(1);
	transition: all 0.6s ease-out 0s;
}
.portfolio:hover img {
	-webkit-transform: scale(1.1);
	-moz-transform: scale(1.1);
	-ms-transform: scale(1.1);
	/* transform: scale(1.06); */
	transform: rotate(1deg) scale(1.1);
	transition: all 0.6s ease-out 0s;	
}

/*---------------------------------------
FOOTER           
-----------------------------------------*/

.footer-top {
	background-color: #1a1a1a;
}

.inner-footer {
	padding-top: 60px;
	padding-bottom: 60px;
}

.inner-footer img {
	width: 50px;
}

.inner-footer img:hover {
	transition: transform .5s ease;
	transform: scale(1.2);
}

.inner-footer p {
	color: #cacaca;
	font-size: 14px;
	line-height: 25px;
	margin-top: 15px;
}

.inner-footer ul {
	margin: 0;
	padding: 0;
	list-style: none;
	text-align: center;
	margin-top: 30px;
}

.inner-footer ul li {
	display: inline;
}

.inner-footer ul li a {
	display: inline-block;
}

.inner-footer ul li a i {
	color: #fff;
	font-size: 16px;
	border: 1px solid #fff;
	width: 30px;
	height: 30px;
	line-height: 29px;
	margin-left: 3px;
	margin-right: 3px;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.inner-footer ul li a:hover i {
	background-color: #1cbac8;
	border-color: #1cbac8;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.footer-bottom {
	background-color: #111;
	padding-top: 25px;
	padding-bottom: 25px;
}

.left-menu {}

.left-menu ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.left-menu ul li {
	display: inline;
}

.left-menu ul li a {
	display: inline-block;
	font-size: 12px;
	text-transform: uppercase;
	color: #cacaca;
	letter-spacing: 1px;
	padding-right: 10px;
	padding-left: 10px;
	text-decoration: none;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
}

.left-menu ul li:first-child {
	padding-left: 0px;
}

.left-menu ul li a:hover {
	color: #1cbac8;
}

.copyright {
	text-align: right;
}

.copyright p {
	margin: 0px;
	color: #cacaca;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 1px;
}

@media (max-width: 991px) {
	.copyright {
		text-align: center;
	}

	.left-menu {
		text-align: center;
		padding-bottom: 20px;
	}
}

/*---------------------------------------
CONTACT SECTION          
-----------------------------------------*/
.contact-section {
	padding-top: 100px;
	padding-bottom: 100px;
	background-color: #f8f8f8;
	text-align: center;
}

.contact-title h2 {
	font-size: 16px;
	text-transform: uppercase;
	font-weight: 600;
	letter-spacing: 1px;
	margin: 0px;
	margin-bottom: 40px;
}

.contact-title h2 span {
	color: #1cbac8;
}

.contact {
	position: relative;
	text-align: center;
	-webkit-transition: .3s;
	transition: .3s;
	padding: 30px 20px;
}

.contact:before {
	position: absolute;
	width: 0;
	height: 0;
	background-color: #1cbac8;
	left: 0;
	top: 0;
	content: "";
	-webkit-transition: .3s;
	transition: .3s
}

.contact:hover:after,
.contact:hover:before {
	width: 30%;
	height: 2px;
	-webkit-transition: .3s;
	transition: .3s
}

.contact:hover {
	box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, 0.10)
}

.contact i {
	font-size: 30px;
	color: rgb(88, 88, 88);
	margin-bottom: 30px;
	-webkit-transition: .3s;
	transition: .3s
}

.contact:hover i {
	color: #1cbac8;
}

.contact h4 {
	font-size: 20px;
	font-weight: 400;
	text-transform: capitalize;
}

.contact p {
	font-size: 15px;
	line-height: 1.8;
}

/*----------------------------------------------*/
/*	Skill Bars
/*----------------------------------------------*/

.bars {
	width: 95%;
	float: left;
	padding: 0;
	text-align: left;
}

.bars .skills {
	margin-top: 36px;
	list-style: none;
}

.bars li {
	position: relative;
	margin-bottom: 40px;
	background: #ccc;
	height: 5px;
	border-radius: 3px;
}

.bars li em {
	font: 14px 'opensans-bold', sans-serif;
	color: #313131;
	text-transform: uppercase;
	letter-spacing: 2px;
	font-weight: normal;
	position: relative;
	top: -25px;
}

.bar-expand {
	position: absolute;
	left: 0;
	top: 0;
	margin: 0;
	padding-right: 24px;
	background: #1cbac8;
	display: inline-block;
	height: 5px;
	line-height: 5px;
	border-radius: 3px 0 0 3px;
}